import useGetVaultList from '@/hooks/vaults/useGetVaultList'
import useVaultsStore from '@/store/vaults/useVaultsList'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { HTextLabelBox } from '@cetus/ui-kit'
import { isAvailableObject } from '@cetus/utils'
import { Button, HStack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import { VaultsAprTooltipContent } from '../list/common/VaultsAprBlock'
import VaultModal from '../modal/index'

type VaultBannerProps = {
  displayTokenA?: Token
  displayTokenB?: Token
  feeDisplay: string
  clmmPool: string
  vaultId: string
  isReverse: boolean
  isUnstableVault: boolean
}
function vaultBanner(props: VaultBannerProps) {
  const [isOpen, setIsOpen] = useState(false)
  const { displayTokenA, displayTokenB, feeDisplay, clmmPool, vaultId, isReverse, isUnstableVault } = props
  const [vaultsAPY, setVaultsAPY] = useState('')
  const { vaultStatsObj } = useVaultsStore()
  const { getVaultsStats } = useGetVaultList()
  useEffect(() => {
    if (isAvailableObject(vaultStatsObj) && vaultStatsObj[clmmPool]) {
      const statsItem = vaultStatsObj[clmmPool]
      setVaultsAPY(statsItem.category == 'cetus' ? statsItem.vaultsTotalApyDisplay : statsItem.vaultsApyDisplay)
    } else {
      getVaultsStats()
    }
  }, [vaultStatsObj, clmmPool])

  const { isApp } = useWindowWidth()
  return (
    <>
      <VStack
        width={isApp ? '100%' : '460px'}
        height="128px"
        backgroundImage="/images/bg_autorebalancer@2x.png"
        backgroundSize="100% 100%"
        padding="12px 16px 20px"
        flexDirection="column"
        justifyContent="space-between"
      >
        <HStack width="100%" justifyContent="space-between" alignItems="center">
          <Text color="primary" fontWeight="500">
            Join via an Auto Vault{' '}
          </Text>
          <HStack>
            <HTextLabelBox
              wrapStyle={{
                w: 'unset',
                gap: '6px'
              }}
              label="APY:"
              labelStyle={{
                fontSize: '14px',
                color: 'text_highlight',
                fontWeight: '500'
              }}
              rightValueTip={
                <VaultsAprTooltipContent
                  vaultsLstApyDisplay={vaultStatsObj[clmmPool]?.vaultsLstApyDisplay || ''}
                  vaultsApyDisplay={vaultStatsObj[clmmPool]?.vaultsApyDisplay || ''}
                  vaultsTotalApyDisplay={vaultStatsObj[clmmPool]?.vaultsTotalApyDisplay || ''}
                  vaultsAprDisplay={vaultStatsObj[clmmPool]?.vaultsAprDisplay}
                  category={vaultStatsObj[clmmPool]?.category}
                />
              }
              value={vaultsAPY || '-'}
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500'
              }}
            />
            <Button width="88px" height="24px" borderRadius="4px" fontSize="12px" onClick={() => setIsOpen(true)} fontWeight="500">
              Deposit
            </Button>
          </HStack>
        </HStack>
        <HStack w="100%" mt="14px">
          <Text lineHeight="20px" maxWidth="290px" fontSize="12px">
            Automate your LPing by selecting a {isUnstableVault ? '3rd party' : ''} strategy provider and enjoy higher yields powered by experts!{' '}
          </Text>
        </HStack>
      </VStack>
      <VaultModal
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        displayTokenA={displayTokenA}
        displayTokenB={displayTokenB}
        feeDisplay={feeDisplay}
        clmmPool={clmmPool}
        vaultId={vaultId}
        isReverse={isReverse}
        isUnstableVault={isUnstableVault}
      />
    </>
  )
}
export default vaultBanner
